import React from "react";
import YouTube from 'react-youtube';

class PlayMusicButton extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "",
      player: {},
      playerState: 0
    };

    this.handleClick = this.handleClick.bind(this);
    this.onReady = this.onReady.bind(this);
    this.onStateChange = this.onStateChange.bind(this);
  }

  handleClick() {
    this.state.player.pauseVideo && this.state.playerState === 1 && this.state.player.pauseVideo();
    this.state.player.playVideo && this.state.playerState !== 1 && this.state.player.playVideo();
  }

  onReady(event) {
    // access to player in all event handlers via event.target
    this.setState({
      title: event.target.getVideoData().title,
      player: event.target
    });
  }

  onStateChange(event) {
    // access to player in all event handlers via event.target
    this.setState({ playerState: event.target.getPlayerState() });
  }

  render() {
    return (
      <div className="row">
        <div className="position-absolute" style={{ top: -99999 }}>
          <YouTube videoId="5qap5aO4i9A"
            opts={{
              height: '450',
              width: '800',
              playerVars: {
                playlist: "5qap5aO4i9A",
                loop: 1
              }
            }}
            onStateChange={this.onStateChange}
            onReady={this.onReady} />
        </div>
        <div className="col-12">
          <button type="button" className="btn btn-outline-info d-inline-block mr-2" onClick={this.handleClick}>{this.state.playerState === 1 ? <i className="fas fa-pause"></i> : <i className="fas fa-play"></i>}</button>
          <span>{this.state.title}</span>
        </div>
      </div>
    );
  }
}

export default PlayMusicButton;
